@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Nav {
  display: grid;
  grid-template-rows: 1.5fr 1fr;
  height: 80%;
  width: 100%;
  background-color: #fff;
}

.bottom {
  display: flex;
  align-self: center;
  height: 70%;
  color: #d6d8da;
}
.bottom button {
  cursor: pointer;
  font-family: "Open Sans", sans-serif;
  background-color: white;
  border: white;
  font-size: 14px;
  border-radius: 5px;
}

.bottom button:hover {
  background-color: #f3f1f1;
}
.top {
  display: flex;
  justify-content: center;
  align-items: center;
  border-bottom: 1px solid #d6d8da;
}
.cnt {
  display: flex;
  flex-direction: row;
  width: 10rem;
  justify-content: space-evenly;
}
.cnt button {
  cursor: pointer;
  background-color: white;
  border: none;
  height: 2.5rem;
  width: 2.5rem;
  border-radius: 50%;
}
.cnt button:hover {
  background-color: #f3f1f1;
}

.cnt .content {
  cursor: pointer;
  padding: 1px 5px;
  border-radius: 5px;
  position: relative;
}
.content:hover {
  background-color: #f3f1f1;
}
.text {
  font-family: "Open Sans", sans-serif;
  font-size: 13px;
  position: absolute;
  top: 25%;
  left: 27%;
}
.arrow {
  display: inline-block;
  padding: 3px;
}

.right {
  border: solid rgb(32, 32, 32);
  border-width: 0 3px 3px 0;
  transform: rotate(-45deg);
  -webkit-transform: rotate(-45deg);
}

.left {
  border: solid rgb(32, 32, 32);
  border-width: 0 3px 3px 0;
  transform: rotate(135deg);
  -webkit-transform: rotate(135deg);
}
.leftInactive {
  border: solid rgb(177, 175, 175);
  border-width: 0 3px 3px 0;
  transform: rotate(135deg);
  -webkit-transform: rotate(135deg);
}
.HiddenTop {
  position: absolute;
  z-index: 100;
  background-color: #fff;
  height: 0%;
  width: 100%;
  box-shadow: 0 2px 4px 0 rgb(0 0 0 / 50%);
  overflow-y: hidden;
  transition: 0.5s;
  display: grid;
  grid-template-rows: 1fr 7fr 2fr;
}
.HiddenTop .hiddenparent {
  grid-row: 2/3;
}
.HiddenTop .closebtn {
  grid-row: 3/4;
  display: flex;
  place-self: center;
  height: 35px;
  width: 35px;
  border-radius: 50%;
  position: relative;
}

.closebtnchild {
  position: absolute;
  left: 5px;
  top: 5px;
  height: 24px;
  width: 24px;
  cursor: pointer;
  opacity: 0.54;
  background: no-repeat
    url(data:image/svg+xml;base64,PHN2ZyBmaWxsPSIjMDAwMDAwIiBoZWlnaHQ9IjI0cHgiIHZpZXdCb3g9IjAgMCAyNCAyNCIgd2lkdGg9IjI0cHgiIHhtbG5zPSJodHRwOi8vd3d3LnczLm9yZy8yMDAwL3N2ZyI+PHBhdGggZD0iTTcuNDEgMTUuNDFMMTIgMTAuODNsNC41OSA0LjU4TDE4IDE0bC02LTYtNiA2eiIvPjxwYXRoIGQ9Ik0wIDBoMjR2MjRIMHoiIGZpbGw9Im5vbmUiLz48L3N2Zz4=);
}
.closebtn:hover {
  transition: 0.2s ease-in;
  background-color: #f3f1f1;
}

.btn {
  border: none;
  background-color: white;
  cursor: pointer;
  height: 37px;
  width: 37px;
  margin: 0.5rem 0.3rem;
  border-radius: 50%;
}
.btn:hover {
  transform: scale(1.1, 1.1);
  background-color: #f1f3f4;
}
.isActive {
  border: none;
  cursor: pointer;
  height: 37px;
  width: 37px;
  margin: 0.5rem 0.3rem;
  border-radius: 50%;
  background-color: #3d4042;
}

.isActive:hover {
  background-color: #484a4b;
}

.container {
  display: grid;
  height: 100vh;
  width: 100vw;
  grid-template-columns: auto;
  grid-template-rows: 1.5fr 8fr;
  background-color: #f1f3f4;
}
.test {
  grid-column: 2/3;
  grid-row: 2/3;
}
.Navbar {
  grid-column: 1/3;
  grid-row: 1/2;
}
.canvas {
  background-color: #fff;
  cursor: crosshair;
  border: 2px solid #dadce0;
  border-radius: 3px;
}
.options {
  grid-column: 1;
  grid-row: 2/3;
  display: flex;
  align-items: center;
  position: relative;
}
.Box {
  position: absolute;
  left: 10%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: white;
  border-radius: 5rem;
  border: 1px solid #dadce0;
}
.InvincibleBox {
  display: none;
}

